<template>
  <div>
      <v-card>
        <v-card-title>
            <h3>User Quiz</h3>
        </v-card-title>
        <v-card-text>
            <h4>Question {{numInc+1}}: {{allQuizzes.questions?allQuizzes.questions[numInc]:''}}</h4>
            <v-list>
                <v-list-item v-for="(answer, ind) in allQuizzes.answers" :key="ind">{{String.fromCharCode('A'.charCodeAt(0)+ind)}}: {{answer[numInc].answer}}</v-list-item>
            </v-list>
        </v-card-text>
        <v-card-actions>
            <v-btn color="primary" :disabled="numInc < 1" @click="numInc--"><</v-btn>
            <v-btn color="primary" :disabled="allQuizzes.questions?numInc >= allQuizzes.answers.length-1:false" @click="numInc < allQuizzes.answers.length-1? numInc++:numInc = allQuizzes.answers.length-1">></v-btn>
        </v-card-actions>
      </v-card>
  </div>
</template>

<script>
export default {
    name:'userquiz',
    data:()=>({
        numInc:0,
        adjustedQuizzes:[],
        allQuizzes:[],
        responseMsg:'',
    }),
    beforeMount(){
        this.viewQuiz()
    },
    methods:{
        adjustQuizzes(){
            if(this.numInc)
            this.numInc++
        },
        async viewQuiz(){
           let request = await this.$root.fetchData('POST', "/viewquiz", 
              {
                  username:this.$store.state.username
              }
            )
            this.allQuizzes = request
            this.responseMsg = request
        }
    }
}
</script>

